import config from '@/config/config';
export default {
	data() {
		return {
			userKey: '',
			id: 0,
			indicatorDots: true,
			autoplay: false,
			mainData: {},
			shareData: {
				title: '',
				imgUrl: '',
				intro: '',
				url: '',
				baseUrl: ''
			},
			price: 0,
			categoryId: 0,
			brandModelId: 0,
			standardId: 0,
			skuId: 0,
			skuName: '',
			skuImg: '',
			num: 1,
			type: 0,
			scrollbar: true,
			swiper_class: {
				height: '400upx'
			},
			qrCodeUrl: '',
			isH5: false
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		var user = uni.getStorageSync('uerInfo');
		if (user != '') {
			this.userKey = user.userKey;
		}
		if (option.id) this.id = parseInt(option.id);
		this.getGoods();


		wx.showShareMenu({
			withShareTicket: true,
			menus: ['shareAppMessage', 'shareTimeline']
		});






		this.setShare();
	},
	onShareAppMessage(res) {
		this.setShare();
		return {
			path: this.shareData.baseUrl,
			title: this.shareData.title,
			imageUrl: ''
		};
	},
	onShareTimeline(res) { //分享到朋友圈
		this.setShare();
		let distSource = uni.getStorageSync('distSource');
		if (distSource) {
			return {
				title: this.shareData.title,
				type: 0,
				query: this.shareData.baseUrl,
				imageUrl: this.shareData.imgUrl
			}
		}
	},
	methods: {
		setShare() {
			var img = config.SITE + 'static/logo.png';
			img = this.mainData.goods_imgs;
			this.shareData.title = '爱秀装' + this.mainData.BrandModelName + '详情';
			this.shareData.intro = this.mainData.BrandModelName + '同一风格，上百件家具任意组合，比市场便宜50%';
			this.shareData.imgUrl = img;
			this.shareData.baseUrl = 'pages/furniture/info?id=' + this.id;
			this.shareData.url = config.SITE + '#/' + this.shareData.baseUrl;
		},
		selectSku(e) {
			//选择规格
			this.skuId = e.StandardId;
			this.skuName = e.StandardName;
			this.price = e.UnitPrice;
			if (e.ImgUrl) this.skuImg = e.ImgUrl;
		},
		showCart(type) {
			this.type = type;
			//购物车显示
			this.$nextTick(() => {
				this.$refs['sku'].open();
			});
			// }
		},
		numberChange(e) {
			this.num = parseInt(e);
		},
		getGoods() {
			//获取材料类别
			var _this = this;
			uni.showLoading({
				title: '数据查询中'
			});
			_this.$request.shop
				.getGoods({
					getgoldGoods_id: _this.id
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						_this.mainData = data.data;
						_this.price = data.data.money;

						uni.setNavigationBarTitle({
							title: _this.mainData.BrandModelName
						});
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		saveOrder() {
			//订单支付
			uni.navigateTo({
				url: '/pages/shop/confirmOrder?order=' + this.id + '_' + this.num
			});
		}
	}
};
